"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { useAuth } from "@/context/AuthContext";
import { roomsApi, Match } from "@/lib/api";
import { VictoryBanner } from "@/components/dashboard/VictoryBanner";
import { ActiveDirectives } from "@/components/dashboard/ActiveDirectives";
import { CombatLogPreview } from "@/components/dashboard/CombatLogPreview";
import { Crown, Users } from "lucide-react";

export default function DashboardPage() {
    const { user } = useAuth();
    const [recentMatches, setRecentMatches] = useState<Match[]>([]);
    const [lastMatch, setLastMatch] = useState<Match | null>(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            const { data } = await roomsApi.getHistory(1, 5);
            if (data?.matches) {
                setRecentMatches(data.matches);
                setLastMatch(data.matches[0] || null);
            }
            setIsLoading(false);
        };
        fetchData();
    }, []);

    return (
        <div className="p-8">
            {/* Last Match Banner */}
            {lastMatch && <VictoryBanner match={lastMatch} />}

            {/* Welcome banner for new users */}
            {!lastMatch && !isLoading && (
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="bg-gradient-to-r from-accent to-accent-dark rounded-lg p-8 mb-8 text-center"
                >
                    <Crown className="w-16 h-16 text-white mx-auto mb-4" />
                    <h2 className="font-display text-4xl text-white mb-2">
                        WELCOME, {user?.username?.toUpperCase()}
                    </h2>
                    <p className="text-white/80 mb-6">
                        Ready to begin your journey to grandmaster?
                    </p>
                    <Link href="/play">
                        <Button className="bg-black hover:bg-gray-900" size="lg">
                            START YOUR FIRST GAME
                        </Button>
                    </Link>
                </motion.div>
            )}

            {/* Active Directives */}
            <ActiveDirectives />

            {/* Combat Log + Allies */}
            <div className="grid grid-cols-3 gap-8">
                {/* Combat Log */}
                <CombatLogPreview matches={recentMatches} />

                {/* Allies Online */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.3 }}
                >
                    <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-2">
                            <div className="w-1 h-6 bg-accent" />
                            <h3 className="font-display text-xl">ALLIES ONLINE</h3>
                        </div>
                        <div className="flex items-center gap-1 text-xs text-success">
                            <div className="w-2 h-2 rounded-full bg-success" />
                            03 ACTIVE
                        </div>
                    </div>

                    <Card className="p-4">
                        {[
                            { name: "Queen_Gambit", status: "LOBBY", online: true },
                            { name: "Kasparov_Jr", status: "IN MATCH", online: true },
                            { name: "Bishop_Takes", status: "PUZZLE RUSH", online: true },
                        ].map((ally, index) => (
                            <div
                                key={index}
                                className="flex items-center gap-3 py-3 border-b border-border last:border-0"
                            >
                                <div className="relative">
                                    <Avatar className="w-10 h-10">
                                        <AvatarFallback>{ally.name.slice(0, 2)}</AvatarFallback>
                                    </Avatar>
                                    <div className="absolute bottom-0 right-0 w-3 h-3 bg-success rounded-full border-2 border-background-card" />
                                </div>
                                <div className="flex-1">
                                    <div className="font-medium text-white text-sm">{ally.name}</div>
                                    <div
                                        className={`text-xs ${ally.status === "IN MATCH"
                                                ? "text-accent"
                                                : "text-foreground-dim"
                                            }`}
                                    >
                                        {ally.status}
                                    </div>
                                </div>
                            </div>
                        ))}

                        <button className="flex items-center gap-2 text-foreground-muted hover:text-white mt-4 text-sm w-full justify-center py-2 border border-dashed border-border rounded hover:border-accent transition-colors">
                            <Users className="w-4 h-4" />
                            ADD RIVAL
                        </button>
                    </Card>
                </motion.div>
            </div>
        </div>
    );
}
